import axios from "axios";
import jwt_decode from "jwt-decode";
import router from "@/router";
export default {
  data() {
    return {
      email: "",
      password: "",
      error: false,
      sucess: "",
      baseUrl: "https://production-api-rentable.onrender.com/users/login",
    };
  },
  mounted() {
        document.addEventListener('keyup', this.verifyEnter);

  },

  methods: {

    async verifyEnter() {
         if (event.key === 'Enter' || event.keyCode === 13) {
        // Coloque aqui a função que você deseja executar
        this.login();
      }
    },
    
    async login() {
      
      this.error = "";
      await axios
        .post(`${this.baseUrl}`, {
          email: this.email,
          password: this.password
        })
        .then((res) => {
        
          console.log(res.data.token);

          const decodedToken = jwt_decode(res.data.token);

          

          localStorage.setItem("id", decodedToken._id);
          localStorage.setItem("photoUser", decodedToken.photo);
          localStorage.setItem("contact", decodedToken.contact);

          this.sucess = "You are on ✔";

          setTimeout(() => {
            this.$router.push("/");
          }, 3000);

          localStorage.setItem("token", res.data.token);
        })
        .catch((err) => {
          if (!Array.isArray(err.response.data.message)) {
            this.error = err.response.data.message;
          } else {
            this.error = err.response.data.message[0];
          }
          
        });
    },
  },
};
